html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

body {
  font-family: Montserrat;
  color: #000000;
  margin: 0;
}

.hero {
  width: 100%;
  position: relative;
}

.hero__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header {
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  font-size: 18px;
  font-weight: 500;
  text-transform: capitalize;
  background-color: rgba(255, 255, 255, 0.7);
  display: flex;
  margin-top: 30px;
}
.header__left {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 20px;
}
.header__right {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
}
.header__logo {
  padding: 10px 20px;
}
.header__flags {
  padding: 10px 20px 10px 10px;
}

.menu__checkbox {
  position: absolute;
  left: -9999px;
}

.menu__toggler {
  cursor: pointer;
}
@media screen and (min-width: 800px) {
  .menu__toggler {
    display: none;
  }
}

.menu__list {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media screen and (min-width: 800px) {
  .menu__list {
    display: flex !important;
  }
}

.menu__link {
  display: block;
  padding: 20px;
  text-decoration: none;
}

.menu__link:hover {
  color: white;
  background-color: #CD104D;
}

.menu__checkbox:checked ~ .menu__list {
  display: block;
}